import React, { CSSProperties, FunctionComponent, ReactNode } from "react"
import {
  CardContent,
  CardMeta,
  CardTitle,
  FeaturedImage,
  StyledArticle,
  StyledCard,
} from "./style"
import Valine from "gatsby-plugin-valine"
import { FireOutlined } from "@ant-design/icons"

export interface CardProps {
  title?: string
  path: string
  featuredImage?: any
  content?: string
  meta?: {
    time: string
    timePretty: string
    tag: string | null
  }
  halfImage?: boolean
  compact?: boolean
  style?: CSSProperties
  children?: ReactNode
}

export const Card: FunctionComponent<CardProps> = ({
  title,
  meta,
  path,
  featuredImage,
  content,
  halfImage = false,
  compact = false,
  style,
  children,
}) => {
  console.log("path", path)
  return (
    <StyledArticle style={style}>
      <StyledCard to={path}>
        {/* TODO: Oh boy... */}
        {featuredImage && featuredImage.fixed && (
          <FeaturedImage sizes={featuredImage.fixed} halfImage={halfImage} />
        )}
        {featuredImage && featuredImage.sizes && (
          <FeaturedImage sizes={featuredImage.sizes} halfImage={halfImage} />
        )}
        <CardContent compact={compact}>
          {children}
          <header>
            {meta && (
              <CardMeta>
                {meta.tag && <>{meta.tag}</>}
                {meta.time && (
                  <time dateTime={meta.time}>{meta.timePretty}</time>
                )}
                <span
                  id={path}
                  className="leancloud_visitors"
                  data-flag-title={title}
                >
                  <FireOutlined style={{ marginRight: 8 }} />
                  <i className="leancloud-visitors-count"></i>
                </span>
              </CardMeta>
            )}
            {title && <CardTitle>{title}</CardTitle>}
          </header>
          {content && <p dangerouslySetInnerHTML={{ __html: content }} />}
          <Valine visitor={true} style={{ display: "none" }}></Valine>
        </CardContent>
      </StyledCard>
    </StyledArticle>
  )
}
